<template>
  <CRow class="justify-content-center">
    <CCol>
      <CCard>
        <CCardHeader class="d-flex justify-content-between">
          <div>
            <slot name="header">
              <CIcon name="cil-healing" />
              <strong>Batches</strong>
            </slot>
          </div>
          <CButton
            variant="outline"
            color="success"
            @click="openModalNewBatch()"
          >
            <CIcon name="cil-plus" class="mt-0" />&nbsp;Add New Batch
          </CButton>
        </CCardHeader>
        <CCardBody>
          <v-client-table :data="rows" :columns="columns" :options="options">
            <div slot="actions" slot-scope="{ row }">
              <CButton
                class="mr-3 mb-1"
                variant="outline"
                @click="openModalEdit(row)"
                color="primary"
                v-c-tooltip="'Edit batch'"
              >
                <CIcon name="cil-pencil" />
              </CButton>
              <CButton
                class="mr-3 mb-1"
                @click="deleteRow(row)"
                variant="outline"
                color="danger"
                v-c-tooltip="'Delete batch'"
              >
                <CIcon name="cil-delete" />
              </CButton>
            </div>
          </v-client-table>
        </CCardBody>
      </CCard>
    </CCol>
    <sweet-modal :blocking="Boolean(true)" ref="modal" overlay-theme="dark">
      <CCard style="text-align: left; border: 1px solid">
        <CCardHeader>
          <strong>{{ title }}</strong>
        </CCardHeader>
        <CForm @submit.prevent="submit">
          <CCardBody>
            <p v-for="(error,index) in errors" :key="index" class="alert alert-danger">{{error}}</p>
            <CSelect
              label="Medicine name"
              placeholder="Select a Medicine"
              @change="changeMedicine($event)"
              :options="meds"
              :class="{ 'form-group--error': $v.medicine.$error }"
              v-model.trim="$v.medicine.$model"
              id="medSelect"
            />
            <div class="error" v-if="!$v.medicine.required">
              Medicine is required
            </div>
            <CSelect
              label="Bill Number"
              placeholder="Select a Bill Number"
              @change="changeBill($event)"
              :options="billNumbers"
              :class="{ 'form-group--error': $v.bill.$error }"
              v-model.trim="$v.bill.$model"
              id="billSelect"
            />
            <div class="error" v-if="!$v.bill.required">
              Bill number is required
            </div>
            <CRow>
              <CCol md="6">
                <CInput
                  :disabled="isEdit"
                  id="bp"
                  :class="{ 'form-group--error': $v.bought.$error }"
                  type="number"
                  step="any"
                  label="Bought Price(Rs.)"
                  v-model.trim="$v.bought.$model"
                />
                <div class="error" v-if="!$v.bought.required">
                  Bought price is required
                </div>
              </CCol>
              <CCol md="6">
                <CInput
                  id="sp"
                  type="number"
                  label="Sell Price(Rs.)"
                  step="any"
                  v-model.trim="$v.sell.$model"
                  :class="{ 'form-group--error': $v.sell.$error }"
                />
                <div class="error" v-if="!$v.sell.required">
                  Selling price is required
                </div>
              </CCol>
            </CRow>
            <CRow>
              <CCol md="6">
                <CInput
                  :disabled="isEdit"
                  id="quantity"
                  type="number"
                  label="Quantity"
                  v-model.trim="$v.qty.$model"
                  :class="{ 'form-group--error': $v.qty.$error }"
                />
                <div class="error" v-if="!$v.qty.required">
                  Quantity is required
                </div>
              </CCol>
              <CCol md="6">
                <CInput
                  id="storage"
                  :class="{ 'form-group--error': $v.storage.$error }"
                  label="Storage"
                  v-model.trim="$v.storage.$model"
                />
                <div class="error" v-if="!$v.storage.required">
                  Storage is required
                </div>
              </CCol>
            </CRow>
            <CInput
              id="date"
              type="date"
              label="Expiary Date"
              v-model.trim="$v.exDate.$model"
              :class="{ 'form-group--error': $v.exDate.$error }"
            />
            <div class="error" v-if="!$v.exDate.required">
              Expiary date is required
            </div>
            <div class="error" v-if="!$v.exDate.minValue">
              Expiary date cant be a previous date
            </div>
          </CCardBody>
          <CCardFooter>
            <CButton
              class="col"
              variant="outline"
              color="success"
              type="submit"
            >
              <CIcon name="cil-check-circle" />&nbsp;Submit
            </CButton>
          </CCardFooter>
        </CForm>
      </CCard>
    </sweet-modal>
  </CRow>
</template>
<script>
import moment from "moment";
import { required } from "vuelidate/lib/validators";
export default {
  name: "Batches",
  data() {
    return {
      medicine: "",
      errors: [],
      title: "",
      isEdit: false,
      bill: "",
      bought: "",
      id: "",
      sell: "",
      qty: "",
      exDate: "",
      storage: "",
      meds: [],
      billNumbers: [],
      rows: [],
      columns: [
        "purchase.bill_number",
        "medicine.name",
        "purchase.supplier.name",
        "expiry_date",
        "bought_price",
        "selling_price",
        "remaining_quantity",
        "storage",
        "actions",
      ],
      options: {
        columnsClasses: {
          actions: "text-center",
          remaining_quantity: "text-center",
          selling_price: "text-center",
          bought_price: "text-center",
          storage: "text-center",
        },
        filterByColumn: true,
        perPage: 10,
        texts: {
          filter: "Filter:",
          filterBy: "Filter by {column}",
          count: " ",
        },
        filterable: [
          "medicine.name",
          "purchase.bill_number",
          "purchase.supplier.name",
        ],
        headings: {
          name: "Name",
          "purchase.bill_number": "Bill Number",
          "purchase.supplier.name": "Supplier",
          "medicine.name": "Name",
        },
      },
    };
  },
  validations: {
    medicine: {
      required,
    },
    bought: {
      required,
    },
    sell: {
      required,
    },
    qty: {
      required,
    },
    exDate: {
      required,
      minValue: value => value > new Date().toISOString()
    },
    storage: {
      required,
    },
    bill: {
      required,
    },
  },
  mounted() {
    console.log(this.$v);
    this.getData();
    this.getMedicine_Bill();
  },
  methods: {
    getData() {
      this.$http
        .get("http://localhost:3000/batch/get")
        .then((res) => {
          res.data.forEach((item) => {
            let local = moment
              .utc(item.expiry_date)
              .local()
              .format("YYYY-MM-DD");
            item.expiry_date = local;
          });
          this.rows = res.data;
          console.log(res.data);
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    async getMedicine_Bill() {
      await this.$http
        .get("http://localhost:3000/medicine/get")
        .then((res) => {
          let array = [];
          res.data.forEach((item) => {
            array.push(item.name + " " + item.size);
          });
          this.meds = array;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
      await this.$http
        .get("http://localhost:3000/purchase/get")
        .then((res) => {
          let array = [];
          res.data.forEach((item) => {
            array.push(item.bill_number);
          });
          this.billNumbers = array;
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
    changeMedicine(e) {
      this.medicine = e.target.value;
    },
    changeBill(e) {
      this.bill = e.target.value;
    },
    submit() {
      this.$v.$touch()
      if (this.$v.$invalid) {
        this.errors.push("Please check the form and submit.");
      } else {
        this.formData = {
          bill_number: this.bill,
          name: this.medicine.split(" ")[0],
          size: this.medicine.split(" ")[1],
          bought_price: this.bought,
          selling_price: this.sell,
          remaining_quantity: this.qty,
          expiry_date: this.exDate,
          storage: this.storage,
        };
        if (this.isEdit === false) {
          this.$http
            .post("http://localhost:3000/batch/post", this.formData)
            .then(() => {
              this.$swal.fire("Done!", "Batch created successfully!", "success");
              this.getData();
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              if(err.response.status == 501) {
                this.$swal.fire("Values have errors", "Exceeds the value of the bill number", "error");
              }else {
                this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              }
              console.log(err.response);
            });
        } else {
          this.$http
            .put("http://localhost:3000/batch/" + this.id, this.formData)
            .then((res) => {
              this.$swal.fire("Done!", "Batch edited successfully!", "success");
              this.getData();
            })
            .catch((err) => {
              // eslint-disable-next-line no-console
              this.$swal.fire("Values have errors", err.response.data.errors[0].msg || "Something went wrong.", "error");
              console.log(err.response);
            });
        }
        this.$refs.modal.close("modal");
      }
    },
    async openModalEdit(row) {
      this.title = "Edit batch form";
      let local = moment.utc(row.expiry_date).local().format("YYYY-MM-DD");
      console.log(row);
      this.isEdit = true;
      this.id = row._id;
      this.$refs.modal.open("modal");
      this.medicine = row.medicine.name;
      this.qty = row.remaining_quantity;
      this.sell = row.selling_price;
      this.bought = row.bought_price;
      this.exDate = local;
      this.bill = row.purchase.bill_number;
      this.storage = row.storage;
      this.errors = []
    },
    async openModalNewBatch() {
      this.title = "New Batch Form";
      this.isEdit = false;
      this.$refs.modal.open("modal");
      this.medicine = "";
      let dropDown1 = document.getElementById("medSelect");
      dropDown1.selectedIndex = 0;
      let dropDown2 = document.getElementById("billSelect");
      dropDown2.selectedIndex = 0;
      this.qty = "";
      this.sell = "";
      this.bought = "";
      this.exDate = "";
      this.bill = "";
      this.storage = "";
      this.errors = []
    },
    deleteRow(row) {
      this.$http
        .delete("http://localhost:3000/batch/" + row._id)
        .then((res) => {
          this.getData();
        })
        .catch((err) => {
          // eslint-disable-next-line no-console
          console.log(err.response);
        });
    },
  },
};
</script>
